import React from 'react'
import { StaticImage } from 'gatsby-plugin-image';
import { Element } from 'react-scroll'

import Box from '../../../components/Box'
import Text from '../../../components/Text'
import Container from '../../../components/Container'
import { responsive, Media } from '../../../components/ThemeProvider/theme';

const CircleContent = ({ title, desc, text, index }) => (
  <Box.Absolute
    width="60%"
    top={responsive('15%', '50%')}
    left={responsive(index ? 'unset' : '32.5%', '50%')}
    right={responsive(index ? '32.5%' : 'unset', '50%')}
    transform={responsive('none', 'translate(-50%, -50%)')}
    color="white"
    textAlign="center"
  >
    <Text.Bold fontSize={responsive('1em', null, '1.5em')}>{title}</Text.Bold>
    <Box.Inline
      fontWeight="bold"
      borderTop="1px solid"
      pt="0.5rem"
      mt="0.25rem"
      fontSize={responsive('1.125em', null, '2em')}
    >{desc}</Box.Inline>
    <Text fontSize={responsive('0.75em', null, '1.25em')}>{text}</Text>
  </Box.Absolute>
)

const content = [
  {
    title: "焦點1",
    desc: "ESG綠色永續",
    text: "全面綠化成為全球趨勢",
    img: <StaticImage src="../left.png" alt="left" />
  },
  {
    title: "焦點2",
    desc: "新興市場",
    text: "收益表現更加亮眼",
    img: <StaticImage src="../right.png" alt="right" />
  },
]

const KeyPoint = () => {
  return (
    <Box position="relative" overflow="hidden">
      <div>
        <Media lessThan="tablet">
          <StaticImage
            src="cover_mobile.jpg"
            alt="手機版背景圖"
          />
        </Media>
        <Media greaterThanOrEqual="tablet">
          <StaticImage
            src="cover_desk.jpg"
            alt="電腦版背景圖"
          />
        </Media>
      </div>
      {content.map(({ title, desc, text, img }, k) => (
        <Box.Absolute
          width={responsive('90%', '25%')}
          bottom={responsive('0%', '50%')}
          left={!k && responsive('0%', '2.5%')}
          right={k && responsive('0%', '2.5%')}
          transform={responsive(k ? 'translate(40%, 50%)' : 'translate(-40%, 50%)', 'translate(0, 50%)')}
          key={k}
        >
          {img}
          <CircleContent index={k} title={title} desc={desc} text={text} />
        </Box.Absolute>
      ))}
      <Container position="absolute" top={responsive('5%', '10%')} left="50%" width="100%" textAlign="center" transform="translateX(-50%)">
        <Text.H1 fontSize={responsive('2em', '2.25em')} fontWeight="bold" color="custom.darkGreen">
          兩大關鍵<Text.Inline color="custom.orange"> 投資焦點</Text.Inline>
        </Text.H1>
      </Container>
    </Box>
  )
}

export default KeyPoint
